// ** React Imports
import React, {useEffect, useState} from 'react'
import '@styles/react/pages/product-detail.scss'
import '@styles/react/libs/editor/editor.scss'
import '../styles/instagram.scss'

//img
import {DragDrop} from "@uppy/react"
import Uppy from "@uppy/core"
import icDelete from "../../../../assets/images/icons/icons/ic_delete.svg"
import imgEmpty from "../../../../assets/images/icons/icons/ic_empty.svg"
import {Card, CardBody} from "reactstrap"
//layout
const InstagramVideo = props => {
    const {socialStory} = props
    const [video_url, setVideoUrl] = useState(socialStory?.video_url ? socialStory.video_url : '')

    useEffect(() => {
        if (socialStory) {
            setVideoUrl(socialStory?.video_url ? socialStory.video_url : '')
        }
    })

    const videoUppy = new Uppy({
        meta: { type: 'video' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['video/*'] }
    })

    videoUppy.on('file-added', async (file) => {
       socialStory.video_url = URL.createObjectURL(file.data)
       socialStory.video_file = file.data
       setVideoUrl(URL.createObjectURL(file.data))
    })
    function chooseVideo() {
        return (<DragDrop uppy={videoUppy} locale={{strings: {dropHereOr: "Thêm video"}}}/>)
    }

    function deleteVideo(e) {
        setVideoUrl('')
        socialStory.video_url = null
        socialStory.video_file = null
    }

    function renderVideo() {
        if (video_url && video_url !== '') {
            return (
                <div className={'rounded image-container'}>
                    <div className={'image image-instagram'}>
                        <video autoPlay playsInline loop className={'image-video'}>
                            <source src={video_url}/>
                        </video>
                        {
                            <div className={'btn-delete'}>
                                <img src={icDelete} alt="delete" onClick={e => {
                                    deleteVideo(e)
                                }}/>
                            </div>
                        }
                    </div>
                </div>
            )
        } else {
            return (
                <div className={'empty'}>
                    <div className={'empty-image'}>
                        <img className={'img-contain'} src={imgEmpty} alt="empty"/>
                        <p>
                            Chọn Video
                        </p>
                    </div>
                </div>
            )
        }
    }
    return (
        <>
            <Card className={'border'}>
                <CardBody>
                    <h3 className={'instagram-info'}>Video tải lên</h3>
                    <div className={'instagram--add-image'}>
                        {
                            chooseVideo()
                        }
                        {
                            renderVideo()
                        }
                    </div>
                </CardBody>
            </Card>
        </>
    )
}

export default InstagramVideo
