// ** React Imports
import {useEffect, useState} from 'react'
import '@styles/react/pages/product-detail.scss'
import '@styles/react/libs/editor/editor.scss'
import './styles/instagram.scss'
import {
  Row, Col, Card, CardBody, Modal
} from 'reactstrap'
//img
import InstagramInfo from "./component/InstagramInfo"
import InstagramImage from "./component/InstagramImage"
import InstagramVideo from "./component/InstagramVideo"
//layout
const Instagram = props => {
  // ** Props
  const { socialStory, dispatch } = props
  // ** State
  // ** Condition btn tag
  useEffect(() => {
  })
  return (
    <Row className='my-2'>
      <Col className={`instagram`}>
        <Row>
          <Col lg={12}> <InstagramInfo dispatch={dispatch} socialStory = {socialStory}/></Col>
        </Row>
        <Row>
          <Col lg={6}> <InstagramImage dispatch={dispatch} socialStory = {socialStory}/></Col>
          <Col lg={6}> <InstagramVideo dispatch={dispatch} socialStory = {socialStory}/></Col>
        </Row>
      </Col>
    </Row>
  )
}

export default Instagram
