// ** React Imports
import '../assets/scss/footer-product.scss'
import {useHistory} from "react-router-dom"
const CommonFooter = props => {
    const history = useHistory()
    // ** Props
    const { handleSubmitButton, isLoading} = props
    function handleBack() {
        history?.go(-1)
    }

    return (
        <div>
            <div className={'footer-product d-flex justify-content-end'}>
                <button className={'btn btn-black'} onClick={handleBack} disabled={isLoading}>Quay lại</button>
                <button className={'btn btn-black'} onClick={handleSubmitButton} disabled={isLoading}>Lưu</button>
            </div>
        </div>
    )
}

export default CommonFooter
