// ** React Imports
import React, {useEffect, useState} from 'react'
// import '@styles/react/pages/product-detail.scss'
import '@styles/react/libs/editor/editor.scss'
import '../styles/instagram.scss'
import {
    Row, Col, Card, CardBody, Modal, Input
} from 'reactstrap'
//img
import {DragDrop} from "@uppy/react"
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import icDelete from "../../../../assets/images/icons/icons/ic_delete.svg"
import imgEmpty from "../../../../assets/images/icons/icons/ic_empty.svg"
import {uploadImage} from "../../store/action"
//layout
const InstagramImage = props => {
    const {socialStory} = props
    const [image_url, setImageUrl] = useState(socialStory?.image_url ? socialStory.image_url : '')

    useEffect(() => {
        if (socialStory) {
            setImageUrl(socialStory?.image_url ? socialStory.image_url : '')
        }
    })

    const imageUppy = new Uppy({
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'] }
    })

    imageUppy.use(thumbnailGenerator)

    imageUppy.on('thumbnail:generated', async (file, preview) => {
        socialStory.image_file = file.data
        socialStory.image_url = preview
        socialStory.image_url_thumb = preview
        setImageUrl(preview)
    })
    function chooseImage() {
        return (<DragDrop uppy={imageUppy} locale={{strings: {dropHereOr: "Thêm ảnh"}}}/>)
    }

    function deleteImage(event) {
        setImageUrl('')
        socialStory.image_url = null
        socialStory.image_url_thumb = null
    }

    function renderImage() {
        if (image_url && image_url !== '') {
            return (
                <div className={'rounded image-container'}>
                    <div className={'image image-instagram'}>
                            <img src={image_url} alt="imgProduct" className={'img'}/>
                            {
                                <div className={'btn-delete'}>
                                    <img src={icDelete} alt="delete" onClick={e => {
                                        deleteImage(e)
                                    }}/>
                                </div>
                            }
                    </div>
                </div>
            )
        } else {
            return (
                <div className={'empty'}>
                    <div className={'empty-image'}>
                        <img className={'img-contain'} src={imgEmpty} alt="empty"/>
                        <p>
                            Chọn hình
                        </p>
                    </div>
                </div>
            )
        }
    }
    return (
        <>
            <Card className={'border'}>
                <CardBody>
                    <h3 className={'instagram-info'}>Hình tải lên</h3>
                    <div className={'instagram--add-image'}>
                        {
                            chooseImage()
                        }
                        {
                            renderImage()
                        }
                    </div>
                </CardBody>
            </Card>
        </>
    )
}

export default InstagramImage
