// ** React Imports
import {useEffect, useState} from 'react'
import '@styles/react/pages/product-detail.scss'
import '@styles/react/libs/editor/editor.scss'
import '../styles/instagram.scss'
import {
    Row, Col, Card, CardBody, Modal, Input, Label, Form, FormGroup
} from 'reactstrap'
//img

import InstagramAvatar from "./InstagramAvatar"

//layout
const InstagramInfo = props => {
    const {dispatch, socialStory} = props

    function handleChangeName(value) {
        socialStory.name = value
    }

    function handleChangeSocial(value) {
        socialStory.social_link = value
    }

    return (
       <>
           <Card>
               <CardBody>
                   <div className={'instagram-container'}>
                       <Row>
                           <Col>
                               <div className={'d-flex align-items-center h-100'}>
                                <InstagramAvatar socialStory={socialStory}/>
                               </div>
                           </Col>
                           <Col lg={10}>
                               <h3 className={'instagram-info'}>Thông tin người dùng</h3>
                               <div className={'instagram-product'}>
                                   <FormGroup className={'input-contain'}>
                                       <Label className={'name'}>Tên </Label>
                                       <Input id='name' name='name' type='temperature'
                                              defaultValue={socialStory &&  socialStory.name}
                                              onChange={e => handleChangeName(e.target.value)}/>
                                   </FormGroup>
                                   <FormGroup className={'input-contain'}>
                                       <Label className={'name'}>Đường dẫn vào instagram</Label>
                                       <Input name='social_url' type='temperature' id='social_url'
                                              defaultValue={socialStory &&  socialStory.social_link}
                                              onChange={e => handleChangeSocial(e.target.value)}/>
                                   </FormGroup>
                               </div>
                           </Col>
                       </Row>
                   </div>
               </CardBody>
           </Card>
       </>
    )
}

export default InstagramInfo
