// ** React Imports
import '@styles/react/pages/product-detail.scss'
import '@styles/react/libs/editor/editor.scss'
import '../styles/instagram.scss'
//img
import {DragDrop} from "@uppy/react"
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import Avatar from "../../../../@core/components/avatar"
import {useEffect, useState} from "react"
import {uploadImage} from "../../store/action"
//layout
const InstagramInfo = props => {
    const {socialStory} = props
    const [avatar_url, setAvatarUrl] = useState(socialStory?.avatar_url_thumb ? socialStory.avatar_url_thumb : '')

    useEffect(() => {
        if (socialStory) {
            setAvatarUrl(socialStory?.avatar_url_thumb ? socialStory.avatar_url_thumb : '')
        }
    })

    const imageUppy = new Uppy({
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'] }
    })

    imageUppy.use(thumbnailGenerator)

    imageUppy.on('thumbnail:generated', async (file, preview) => {
        socialStory.avatar_url_thumb = preview
        socialStory.avatar_url = preview
        socialStory.avatar_file = file.data
        setAvatarUrl(preview)
    })

    const renderPreview = () => {
        const stateNum = Math.floor(Math.random() * 6),
            states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
            color = states[stateNum]
        if (avatar_url && avatar_url !== '') {
            return <Avatar className='m-auto avatar' img={avatar_url} width='100' height='100' />
        } else {
            return <Avatar color={color || 'primary'} className='m-auto avatar' content={socialStory?.name || ''} initials />
        }
    }

    return (
        <div>
            <h3 className={'instagram-name justify-content-center'}>Ảnh đại diện</h3>
            {
                renderPreview()
            }
            <div className={'instagram-btn'}>
                <DragDrop uppy={imageUppy} locale={{
                    strings: {
                        dropHereOr: "Hình ảnh"
                    }
                }}/>
            </div>
        </div>
    )
}

export default InstagramInfo
